import "core-js/modules/es.array.push.js";
export default {
  name: "TreeItem",
  props: {
    treeData: {
      type: Array,
      default: () => []
    },
    treeFirst: {
      //判断是否第一个
      type: Boolean,
      default: false
    }
  },

  data() {
    return {};
  },

  created() {},

  methods: {
    // 添加子元素
    addChild(index) {
      if (this.treeData[index].child) {
        this.treeData[index].child.push({
          name: "1"
        });
      } else {
        this.$set(this.treeData[index], "child", [{
          name: "1"
        }]);
      }
    },

    // 删除子元素
    delChild(index) {
      this.treeData.splice(index, 1);
    }

  }
};